import React, { Component } from "react";

export default class Success extends Component {
    render() {
        return (
            <div >
                <div style={{ backgroundColor: "black" }} className="jumbotron">
                    <h1 className="display-4">Thank you!</h1>
                    <p style={{ color: "white" }} className="lead">You will now stay up to date on our latest deals and special events!</p>
                    <hr className="my-4" />
                    <a className="btn btn-primary btn-lg" href="/" role="button">Back to Home</a>
                </div>
            </div>
        )
    }
}